<template>
    <div class="cs-bszn-update-page" v-bind:style="mianStyle" v-show="is_show_update">
        <div class="cs-bszn-update-hd">
                <span class="el-dialog__title" v-if="isClickrow">查看</span>
                <span class="el-dialog__title" v-else>{{!selectRowData.title ?'新增' :'编辑'}}</span>
            <div class="el-dialog__headerbtn" @click="hideUpdateModel()"><i class="el-dialog__close el-icon el-icon-close"></i></div>
        </div>
        <el-form :rules="rules" ref="ruleForm" :model="selectRowData" >
            <el-form-item prop="title">
                <el-input
                        type="textarea"
                        :disabled="isClickrow"
                        :maxlength="30"
                        :autosize="{ minRows: 1, maxRows: 1}"
                        placeholder="在这里输入标题"
                        v-model="selectRowData.title">
                </el-input>
            </el-form-item>


            <el-form-item prop="content">
                <el-input
                        type="textarea"
                        :disabled="isClickrow"
                        :autosize="{ minRows: 11, maxRows: 14}"
                        placeholder="在这里输入正文"
                        v-model="selectRowData.content">
                </el-input>
            </el-form-item>
        </el-form>


        <div class="cs-bszn-update-dialog-footer">
            <el-button type="primary" v-if="false"  style="margin-left:30%;margin-top:2%;">预览</el-button>
            <el-button :plain="true" type="primary" :disabled="isClickrow" @click="saveContent">保存</el-button>
            <el-button :plain="true" type="primary" :disabled="isClickrow" @click="pushContent">发布</el-button>
        </div>
  </div>


</template>

<script>
	export default{
        props: {
            mapHeight:{
                type:Number,
                default:600
            },
            isShow_bjxq :{
                type:Boolean,
                default:false
            },
            is_show_update:false,
            selectRowData:{},
            isClickrow:false,
        },
        data:function () {
            return {
                mianStyle:"",
                rules: {
                    title: [
                        { required: true, message: '请输入30字以内的标题', trigger: 'blur', max:30 }
                    ],
                    content: [
                        { required: true, message: '请输入正文', trigger: 'blur' }
                    ]
                }
            }
        },
		methods:{
            resetData(flag) {
                this.$emit('clearSelectRowData', flag);
            },
            doPublish() {
                let that = this;
                let token = global.data.getToken();
                if(!token) {
                    return false;
                }

                this.$http.post(global.config.weburl + "api/jwContent/addJWContent.do",{token:token, title:that.selectRowData.title,subjectId:that.selectRowData.subjectId, content:that.selectRowData.content, directPublish:1})
                    .then(
                        function(res){
                            var _data = res.data;
                            if(_data.code==0){
                                this.$message({
                                    type: 'success',
                                    message: '发布办事指南成功!'
                                });
                                that.resetData(5);
                            }else{
                                global.tools.handleError(res.data);
                            }
                        }
                    );
            },
            doPublishEdit() {
                let that = this;
                let token = global.data.getToken();
                if(!token) {
                    return false;
                }

                this.$http.post(global.config.weburl + "api/jwContent/updateJWContent.do",{token:token,id:that.selectRowData.id, title:that.selectRowData.title,subjectId:that.selectRowData.subjectId, content:that.selectRowData.content, directPublish:1})
                    .then(
                        function(res){
                            var _data = res.data;
                            if(_data.code==0){
                                this.$message({
                                    type: 'success',
                                    message: '发布办事指南成功!'
                                });
                                that.resetData(5);
                            }else{
                                global.tools.handleError(res.data);
                            }
                        }
                    );
            },
            doAddData() {
                //添加发布
                let that = this;
                let token = global.data.getToken();
                if(!token) {
                    return false;
                }

                this.$http.post(global.config.weburl + "api/jwContent/addJWContent.do",{token:token, title:that.selectRowData.title,subjectId:that.selectRowData.subjectId, content:that.selectRowData.content})
                .then(
                    function(res){
                        var _data = res.data;
                        if(_data.code==0){
                            this.$message({
                                type: 'success',
                                message: '保存办事指南成功!'
                            });
                            that.resetData(6);
                        }else{
                            global.tools.handleError(res.data);
                        }
                    }
                );
            },
            doUpdateData() {
                //更新
                let that = this;
                let token = global.data.getToken();
                if(!token) {
                    return false;
                }

                this.$http.post(global.config.weburl + "api/jwContent/updateJWContent.do", {token:token,id:that.selectRowData.id, title:that.selectRowData.title,subjectId:that.selectRowData.subjectId, content:that.selectRowData.content})
                    .then(
                            function(res){
                                var _data = res.data;
                                if(_data.code==0){
                                    this.$message({
                                        type: 'success',
                                        message: '更新办事指南成功!'
                                    });
                                    that.resetData(6);
                                }else{
                                    global.tools.handleError(res.data);
                                }
                            }
                    );
            },
            saveContent() {  //保存成功
                var _selectRowData = this.selectRowData;
                //console.log("1111111111111111");

                let that = this;
                that.$refs["ruleForm"].validate((valid) => {
                    if (valid) {

                        if (!_selectRowData.id) {
                            this.doAddData();
                        } else {
                            this.doUpdateData();
                        }
                    } else {
                        return false;
                    }
                })

	      	},
	      	pushContent() {  //发布成功
                var _selectRowData = this.selectRowData;
                let that = this;
                that.$refs["ruleForm"].validate((valid) => {
                    if (valid) {
                        if (!_selectRowData.id) {
                            this.doPublish();
                        } else {
                            this.doPublishEdit();
                        }
                    } else {
                        return false;
                    }
                })
		  	},
            hideUpdateModel() {
                const that = this;
                const Bus = global.eventHub;
                that.$emit('hideUpdateModel');       
            }
		},
        created: function(){
            this.mianStyle = {
                height:this.mapHeight +"px"
            }
            //this.resetData(this.selectRowData);
        }
	}
</script>
<style>

.cs-bszn-update-hd{padding: 0 0 20px;}
.cs-bszn-update-dialog-footer{display: -moz-box; display: -webkit-box;  display: box;display: -moz-flex; display: -webkit-flex;display: flex;flex-direction: row;  flex-wrap: wrap;flex-flow: row wrap;padding: 20px 0 0;justify-content:flex-end;}



</style>


